@import "../mixin/baseImport";
@import "../_sprite/common_sprite";
@import "../_sprite/main_popup_sprite";

@keyframes bounceInParticle {
	0% {
		transform: scale(0.3);
	}
	100% {
		transform: scale(1);
	}
}

@keyframes bounceInItem {
	0% {
		transform: scale(0);
	}
	50% {
		transform: scale(1);
	}
	75% {
		transform: scale(0.97);
	}
	100% {
		transform: scale(1);
	}
}

// reward 공통
%main-reward{position: relative;width: 737px;height: 708px;margin: 60px auto 0;z-index: 1;overflow: hidden;
	&.active{
		.particle{animation-name: bounceInParticle;animation-duration: 0.8s;transform: scale(1);}
		.item{animation-name: bounceInItem;animation-duration: 0.8s;transform: scale(1);}
	}
	.touch-guide{position: absolute;top: 0;left: 0;
		.btn-touch{position: absolute;top: 250px;left: 310px;width: 131px;height: 148px;animation: touch-icon 0.8s steps(4) infinite forwards;background: url("/assets/images/sprite/touch.png") no-repeat 0 0;z-index: 2;}
