//몬스터 치료
.monster-cure{display: none;position: fixed;top: 0;left: 0;height: 0;z-index: 30;min-width: 1024px;width: 100%;
	*{cursor: none;}
	&__outer{position: absolute;top: 75px;left: 50%;width: 1024px;height: 553px;margin-left: -512px; text-align: center;}
	&__inner{}
	.dim{position: relative;width: 748px;height: 553px;overflow: visible;margin: 0 auto;}
	.item{position: absolute;top: 0;left: 0;transform: translate(777px, 309px);
		.icon{display: block;width: 247px;height: 244px;
			@for $i from 1 through 48{
				&-cure-item#{$i}{animation: cure_item1 1s steps(4) infinite forwards;background: url("/assets/images/sprite/cure_item#{$i}.png") no-repeat 0 0;}
			}
		}

	}
}

/*profile*/
.main-profile01 {position: relative;width: 842px;height: 585px;margin: 0 auto;z-index: 1;text-align: center;background: url("/assets/images/main/popup/profile/bg_profile01.png") no-repeat 0 0;

	.tit {position: absolute;top: 68px;right: 49px;}

	.user-info {display: flex;

		&-box {position: relative;padding-top: 141px;width: 350px;height: 585px;text-align: center;box-sizing: border-box;

			.user {position: relative;display: inline-block;width: 218px;height: 218px;z-index: 1;background: url("/assets/images/main/popup/profile/icon_default01.png") no-repeat 0 0;

				.img {width: 218px;height: 218px;display: block;overflow: hidden;border-radius: 100%;

					img {width: 100%;height: 100%;}
				}
			}

			.user-name {display: flex;align-items: center;width: 283px;padding: 0 20px;height: 79px;overflow: hidden;margin: 30px auto 0;text-align: center;box-sizing: border-box; border-radius: 40px;box-shadow: 0px 2px 1.4px 0.6px rgba(0, 0, 0, 0.1);background-color: #fff;

				span {width: 100%;@include font($notoecp, $font-b, 25px, 30px, #9d6e57);@include multi-text-overflow(2);height: 60px;}
			}

			.btn-box {position: absolute;bottom: -23px;left: 0;display: flex;justify-content: center;width: 100%;

				.btn {padding: 0 13px;transition: all 0.3s ease-in;}
			}
		}

		&-lists {flex: 1;flex-wrap: wrap;display: flex;margin-top: -13px;justify-content: center;align-content: flex-start;position: relative;padding-top: 141px;height: 585px;text-align: center;box-sizing: border-box;

			.user-info-list {width: 158px;height: 178px;padding: 13px 23px;

				.btn {display: block;width: 100%;height: 100%;}
			}
		}
	}

	.btn {
		&-user {}

		&-close {position: absolute;top: 22px;right: 35px;width: 42px;height: 42px;border-radius: 10px;transition: background 0.3s;}

		&-save {}

		&-restore {}
	}

	.icon {display: block;width: 100%;height: 100%;

		&-default-profile {background: url("/assets/images/main/popup/profile/icon_default01.png") no-repeat 0 0;}

		&-close {background: url("/assets/images/main/popup/profile/icon_close01.png") no-repeat 0 0;}

		$iconList: yuri, oscar, pepper, leo;
		@each $item in $iconList {
			&-#{$item}-profile {background: url("/assets/images/main/popup/profile/icon_#{$item}01_profile.png") no-repeat 0 0;transition: all 0.3s}
			&-#{$item} {background: url("/assets/images/main/popup/profile/icon_#{$item}01.png") no-repeat 0 0;}
		}
	}
}

